.Header-logo {
  -webkit-animation: Header-logo-spin infinite 5s linear;
          animation: Header-logo-spin infinite 5s linear;
  height: 80px;
}

.Header-header {
  background-color: #222;
  color: white;
}

.Header-title {
  font-size: 1.5em;
  margin: 10px
}

@-webkit-keyframes Header-logo-spin {
  from { -webkit-transform: translateX(-360px) rotate(0deg); transform: translateX(-360px) rotate(0deg);}
  to { -webkit-transform: translateX(360px) rotate(360deg); transform: translateX(360px) rotate(360deg);}
}

@keyframes Header-logo-spin {
  from { -webkit-transform: translateX(-360px) rotate(0deg); transform: translateX(-360px) rotate(0deg);}
  to { -webkit-transform: translateX(360px) rotate(360deg); transform: translateX(360px) rotate(360deg);}
}



